<template>
  <view class="login">
    ello
  </view>    
</template>

<script>
  export default {
    data() {
      return {
        formData: {
          email: '',
          password: ''
        },
        loginImg: '/static/normal.jfif',
        rules: {
          password: {
            rules: [{
                required:true,
                errorMessage: '请输入密码',
              },
            ]
          },
          // 对email字段进行必填验证
          email: {
            rules: [
              {
                  required:true,
                  errorMessage: '请输入邮箱',
                },
              {
              format: 'email',
              errorMessage: '请输入正确的邮箱地址',
            }]
          }
        }
      };
    },
    methods: {
      changeImg(i) {
        if (i == 2) {
          this.loginImg = '/static/denglu1.jfif'
        }
        if (i == 3) {
          this.loginImg = '/static/password.jfif'
        }
      },
      recoverImages() {
        this.loginImg = '/static/normal.jfif'
      },
      submit() {
      			this.$refs.loginForm.validate().then(res=>{
      				console.log('表单数据信息：', res);
      			}).catch(err =>{
      				console.log('表单错误信息：', err);
      			})
      		}
    }
  }
</script>

<style lang="scss">
  .page {
    width: 100%;
    height: 100%;
  }

  .login {
    width: 100%;
    height: 100vh;
    position: relative;
    background: url('@/static/login.png') no-repeat center center fixed;
    background-size: cover;
    box-sizing: border-box;

    .login-content {
      width: 100%;

      .logo {
        display: flex;
        justify-content: center;
        align-items: center;

        image {
          margin-top: 50px;
          margin-top: 20px;
          width: 200px;
          height: 102px;
          // margin-top: 50px;
          // position: absolute;
          // left: 50%;
          // transform: translateX(-50%);
          // top: 6%;
        }
      }

      .login-form {
        position: absolute;
        width: 83%;
        min-height: 54vh;
        background-color: #ffffff;
        // top: 45%;
        left: 50%;
        transform: translate(-50%);
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
        border-radius: 12px;
        margin-top: 20px;

        .avatar-content {
          display: flex;
          justify-content: center;
          margin-top: 30px;

          .avatar {
            height: 112px;
            width: 112px;
            border-radius: 56px;
            border: 2px solid white;
            box-shadow: 0 1px 5px black;
          }
        }

        .login-title-content {
          display: flex;
          justify-content: center;

          .login-title {
            margin-top: 18px;
            font-size: 20px;
            color: #939393;
          }
        }

        .form {
          margin: 0 30px;

          .login-botton {
            margin-top: 25px;
            margin-bottom: 23px;
            color: #ffffff;
            background-color: #54bcbd;

            &:active {
              background-color: #1d928e;
            }
          }
          /deep/.uni-forms-item .is-required {
            	display: none;
            	padding: 0;
          }
          // ::v-deep .uni-forms-item__label{
          // 	visibility: hidden !important;
          // 	padding: 0;
          // }
          /deep/.uni-forms-item__error.msg--active {
            margin-left: 10px;
          }
          /deep/.uni-forms-item {
            margin-bottom: 15px;
          }
        }
      }
    }
  }
</style>
